.app {
  width: 420px;
  height: 850px;
  border-radius: 1rem;
  overflow: hidden;
}
button{
  cursor: pointer;
}
// Category
.category {
  height: 850px;
  position: relative;
  .title{
    display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  }
  h1 {
    text-align: center;
    font-size: 5rem;
    margin-top:7rem;
    color: #102544;
    letter-spacing: 1rem;
  }
  .dodam{
    font-size: 1rem;
    color: #102544;
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 1rem;
  }
}

.category-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 380px;
  button {
    font-size: 1.5rem;
    border-radius: 1rem;
    font-family: "SBAggro", sans-serif;
    padding: 2rem 0;
    width: 350px;
    border: none;
    background-color: #ffffff;
    color: #102544;
    transition: all 0.3s ease;
    opacity: 0.8;
  }
}

//호버 처리
.category-wrap button:hover {
  transform: scale(1.05);
  color: #0269B1;
  opacity: 1;
}

// Quiz page

.quiz-page {
  width: 420px;
  height: 850px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0269B1;

  .title {
    width: 420px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #102544;
    h2 {
      font-size: 1.2rem;
      font-weight: 500;
      opacity: 0.8;
      margin-left: 1rem;
    }
  }
  .quiz-game {
    background-color: #ffffff;
    color: #102544;
    width: 380px;
    padding: 1rem 0;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;

    //질문
    .question {
      width: 350px;
      font-size: 2rem;
      overflow-wrap: anywhere;
      word-break: keep-all;
      margin: 1.5rem;
      font-weight: 700;
      span {
        color: #f55835;
      }
    }
    .inner{
      width: 320px;
      display: flex;
      justify-content: space-between;
      p{
        margin: 5px 0;
      }
    }
  }
  .choices {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    button {
      font-size: 1.5rem;
      width: 350px;
      border-radius: 0.5rem;
      padding: 1.5rem 0.5rem;
      border: none;
      overflow-wrap: anywhere;
      word-break: keep-all;
      font-family: "SBAggro", sans-serif;
      font-weight: 500;
      transition: all 0.3s ease;
      color: #102544;
    }
  }
}

// hover
  .choices button:hover{
    filter: brightness(80%);
  }
.quiz-page .bottom{
  display: flex;
  width: 380px;
  align-items: center;
  flex-direction: column;
}
.quiz-page .bottom{
  .next-button{
    font-family: "SBAggro", sans-serif;
    border: none;
    border-radius: 0.5rem;
    font-size: 1.5rem;
    font-weight: 500;
    padding: 0.5rem;
    transition: all 0.3s ease;
    color: #102544;

  }
  .result{
    font-size: 1.5rem;
    font-family: "SBAggro", sans-serif;
    font-weight: 500;
    color: #102544;
    text-align: center;
    margin: 15px 0;
  }
}
.quiz-page .bottom .sub{
  font-size: 1.2rem;
  overflow-wrap: anywhere;
      word-break: keep-all;
}
// 호버처리
.quiz-page .next-button:hover{
    filter: brightness(80%);
  }
.correct {
  background-color: #4caf50;
  color: white;
}

.wrong {
  background-color: #f44336;
  color: white;
}

button:disabled {
  opacity: 0.8;
}

//result
.result-page{
  height: 850px;
  width: 420px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.result-page .title{
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  width: 350px;
  
  h2{
    font-size: 3rem;
    margin-top: 1rem;
  }
  .result{
    font-size: 2rem;
    color: #102544;
    overflow-wrap: anywhere;
      word-break: keep-all;
      margin-bottom: 5rem;
  }
  .cartegory{
    color: #102544;
    font-size: 1.5rem;
    margin: 0;
  }
}
.result-page button{
  font-size: 1.5rem;
    border-radius: 1rem;
    font-family: "SBAggro", sans-serif;
    padding: 1rem 0;
    width: 350px;
    border: none;
    background-color: #ffffff;
    transition: all 0.3s ease;
    opacity: 0.8;
    margin-bottom: 10rem;
}
//hover
.result-page button:hover{
    opacity: 1;
    transform: scale(1.05);
  }